body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Poppins", "Segoe UI", "Roboto", "Oxygen",
		"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	text-align: center;
	background-color: #e6fff7;
}

ul li{
	display: inline;
}

.responsive {
    max-width: 100%;
    height: auto;
    width: auto\9; /* ie8 */
}

.container {
	zoom: 1; /* IE6&7 */
	justify-content: center;
	position: relative;
}

.container:before,
.container:after {
    content: "";
    display: table;
}

.container:after {
    clear: both;
}

.apple1 {
	height: 160px;
	width: 160px;
}
.apple2 {
	height: 140px;
	width: 140px;
}
.apple3 {
	height: 120px;
	width: 120px;
}
.apple4 {
	height: 100px;
	width: 100px;
}

/*game css*/

.Hangman h1 {
	font-size: 2rem;
	font-weight: 400;
	padding-top: 0;
	color: #158cee;
}

.Hangman li {
	margin: 20px;
}

.Hangman-word {
	letter-spacing: 1em;
	margin: 0.4em -1em 0.2em 0;
	font-size: 1.5rem;
	color: #07ffc9;
}

.Hangman-btns {
	text-align: center;
	color: #158cee;
	display: inline-block;
	font-size: 1.5rem;
}
.Hangman li button {
	height: 160px;
	width: 160px;
	background: none;
	cursor: pointer;
  }


.Hangman button {
	border: none;
	font-family: inherit;
	font-size: 2.5rem;
	cursor: pointer;
	width: 5rem;
	margin: 18px 8px 10px 13px;
	letter-spacing: 1px;
	outline: none;
	background: #07ffc9;
	color: rgb(0, 0, 0);
	border-radius: 10px;
	padding: 0;
}

.Hangman-reset {
	color: #d6dfdf;
	width: 10rem !important;
	background-color: #07ffc9;
	border-color: #07ffc9;
	box-shadow: 0 0 3px #07ffc9;
}

.Hangman li button:hover {
	background-color: #e6fff7;
}

.Hangman button:hover {
	background-color: #fffffa;
}

.Hangman button:disabled {
	color: #d6dfdf;
	background-color: #ff1100;
	border-color: #ff1100;
	box-shadow: 0 0 40px #ff1100;
}

.center {
	width: 50%;
  	margin: 0 auto;
}

.fontSpace {
	font-size: 1.5rem;
	letter-spacing: 1px;
}

.imgArray {
	height: 200px;
	padding: 15px;
	max-height: 200px;
}

.instructions {
	font-size: 2.5em;
	color: #483D8B;
	letter-spacing: 1px;
}

/* For iPad */

@media (max-width: 1200px) {
	.fontSpace {
		font-size: 1.25rem;
		letter-spacing: 1px;
	}

	.instructions {
		font-size: 1.5em;
		color: #483D8B;
		letter-spacing: 1px;
	}

	.imgArray {
		height: 185px;
		min-width: 100%;
		padding: 50px 0 0 0;
		margin: 0;
		max-height: 185px;
	}
	.Hangman li button {
		height: 100px;
		width: 100px;
		margin: none;
		background: none;
		cursor: pointer;
	  }

	.Hangman button {
		font-size: 1.25rem;
	}
	.Hangman-reset {
		width: 5rem !important;
	}

	.apple1 {
		height: 70px;
		width: 70px;
	}
	.apple2 {
		height: 80px;
		width: 80px;
	}
	.apple3 {
		height: 90px;
		width: 90px;
	}
	.apple4 {
		height: 100px;
		width: 100px;
	}
}

/* For mobile */

@media (max-width: 800px) {
	.Hangman h1 {
		font-size: 1em;
	}

	ul {
		padding: 0;
	}
	.Hangman li button {
		height: 75px;
		width: 75px;
	  }

	.Hangman {
		margin-top: 25px;
	}

	.imgArray {
		min-width: 100%;
		padding: 35px 0 20px 0;
		margin: 0;
	}

	.apple1 {
		height: 45px;
		width: 45px;
	}
	.apple2 {
		height: 55px;
		width: 55px;
	}
	.apple3 {
		height: 65px;
		width: 65px;
	}
	.apple4 {
		height: 75px;
		width: 75px;
	}

}